// @category modules

// @function forms
// Custom-styled form elements, including selects, checkboxes, and radio buttons.

// IE9 and newer supported; IE8 and lower will have standard form elements
// :not() targets > IE8, #foo is just a random ID
fieldset:not(#foo) {
	input[type="radio"],
	input[type="checkbox"] {
		float: left;
		height: 1px;
		margin: 0;
		opacity: 0;
		padding: 0;
		width: 1px;

		& + label {
			clear: none;
			display: inline-block;
			padding: 6px 5px 4px 27px;
      width: 100%;
      
			&:before {
				@include icon();
				margin-left: em(-18);
				margin-right: em(10);
			}

			&.inline {
				display: inline-block;
			}
		}
	}

	input[type="radio"] {
		& + label {
			&:before {
				content: $iconRadioUnchecked;
			}
		}
		&:active + label,
		&:focus + label {
			color: color(colorCorporateAction);
		}
		&:checked + label {
			&:before {
				content: $iconRadioChecked;
			}
		}
	}

	input[type="checkbox"] {
		& + label {
			&:before {
				content: $iconCheckboxUnchecked;
			}
		}
		&:active + label,
		&:focus + label {
			color: color(colorCorporateAction);
		}
		&:checked + label {
			&:before {
				content: $iconCheckboxChecked;
			}
		}
	}

	// these decorator styles are for browsers that can have custom selects
	.decorator-select {
		&:after {
			content: url('#{$imagesFolder}bg-select-arrow.png');
			height: $inputHeight;
			pointer-events: none;
			position: absolute;
			right: 0px;
			top: -2px;
			width: 58px;
			z-index: (z(content)+1);
		}

		select {
			outline: none;
		}
	}
}

