fieldset {
	label {
		&.inline {
			display: inline-block;
			padding-right: 5px;
		}
	}
}

.decorator-select {
	background: #e6e6e6;
	border: 2px solid color(mediumGrey);
	border-radius: $formElementBorderRadius;
	@include box-sizing();
	display: inline-block;
	height: $inputHeight;
	overflow: hidden;
	position: relative;
	width: 100%;

	&.validation-error {
		background: #e6e6e6 url('#{$imagesFolder}icon-error.png') em(8) center no-repeat;
		padding: 0 0 0 em(32); //BMC-527 Replaced em(32 16)
	}

	&:active {
		border: 2px solid color(colorCorporateAction);
	}

	&:first-child {
		margin-left: 0;
	}

	select {
		-webkit-appearance: none;
		background: transparent;
		border: none;
		@include box-sizing();
		color: black;
		display: block;
		height: $inputHeight;
		padding: 6px 10px 6px 10px;
		position: relative;
		width: 100%;
		z-index: zIndex(content);
	}
}

input[type="file"] {
	.js & {
		visibility: hidden;
		width: 0px;
	}
}

.form-hidden {
	display: none !important;
}


